import React, { useState } from "react";
import Marquee from "react-fast-marquee";
import time from "./time";


const dataMarquee = [
    {
      id: "●", 
      class: "",
      subtitle: "002 ", 
      title: "DAYS TO MY BIRTHDAY"
    },{
      id: "●", 
      class: "",
      subtitle: "TIME IS ", 
      title:  "19:29"
    },{
      id: "●", 
      class: "",
      subtitle: " HAPPY ", 
      title: "2022! "
    },{
      id: "●",
      class: "space",
      subtitle: " See you ", 
      title: "Space Cowboy "
    },{
      id: "●", 
      class: "",
      subtitle: "CURRENTLY WATCHING: ", 
      title: "HUNTER X HUNTER"
    }
  ];

  const TextScroller = () => (
    <section className="datas">
          <div className="container">
            <div className="row">
            
            <Marquee gradient={false} speed={50}> 
              {dataMarquee.map((caseItem) =>(
                <div className="dt" key={caseItem.id}>
                    <div className="case-details">
                        <span className="small" > {caseItem.id} </span>
                        <span className={caseItem.class}>{caseItem.subtitle}</span>
                        <span className={caseItem.class}>{caseItem.title}</span> 
                    </div>      
                </div>
              ))}
            </Marquee>
            </div>
          </div>
    </section>
  );
  

export default TextScroller;
